import React from 'react';
// import cn from 'classnames';
import {Link} from 'react-router-dom';
import { AiOutlineDown, AiOutlineSearch } from 'react-icons/ai';
import './Header.scss';


const Header = () => {
    return (
        <div className="header_wrap">
            <nav className="top_nav">
                <div className="row">
                    <ul className="company_select">
                        <li>기업우대</li>
                        <li>여행사 우대 <AiOutlineDown className="down" /></li>
                    </ul>
                    <ul className="topmenu">
                        <li><Link to="/login">로그인</Link></li>
                        <li><Link to="/join">회원가입</Link></li>
                        <li>고객센터</li>
                        <li>한국사이트 <AiOutlineDown className="down" /></li>
                        <li>
                            <fieldset>
                                <from action="#" method="post">
                                    <legend>검색창</legend>
                                    <input type="text" />
                                    <button type="submit"><AiOutlineSearch className="search" /></button>
                                </from>
                            </fieldset>
                        </li>
                    </ul>
                </div>
            </nav>
            <div className="logo_nav">
                <div className="row">
                    <h1 className="logo">
                        <Link to="/" className="lnk">
                            <img src="images/jejuairlogo1.png" alt="" />
                        </Link>
                    </h1>
                    <ul className="depth1">
                        <li>항공권 예매</li>
                        <li>마이페이지</li>
                        <li>서비스안내</li>
                        <li>이벤트/새소식</li>
                        <li>여행상품</li>
                        <li>리프레시 포인트</li>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Header;